(function(){
  angular.module('gardeningApp')
    .service('GardenService', GardenService);
  /** @ngInject */
  function GardenService($http, appUrl, Upload){
    var list;
    this.maintenence = function(type, id){
      return $http.get(appUrl + type + '/projects?page=' + id + '');
    }
    this.getGardenById = function(id){
      return $http.get(appUrl + 'project/' + id );
    }
    this.setStorage = function(arg){
      list = arg;
    }
    this.getStorage = function(){
      return list;
    }
    this.metaData = function(){
      return $http.get(appUrl + 'project/meta-data')
    }
    this.uploader = function(file){
      return Upload.upload({
        url: appUrl + 'file/upload',
        data: {
          project_file: file
        }
      });
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('gardeningApp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr, $state) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1458543673656;
    vm.showToastr = showToastr;
    vm.trigger = Trigger.bind(vm);
    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function Trigger(type){
      $state.go(type);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

